.block {
    background: linear-gradient(45deg, #6495ed, #222264 80%) no-repeat;
    max-width: 100%;
    min-height: 100vh;
    color: white;

}

.selectCity {
    padding: 2rem 6rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.currentWeather {
    margin: 2rem 6rem;
}


.cardsBlock {
    background-color: rgba(0, 0, 0, 0.4);
    margin: 2rem 6rem 0;
    padding: 2rem;
    border-radius: 5px;
    box-shadow: 0 10px 30px #535354;
    display: flex;
    flex-direction: column;

}

.cardsBlock h3{
    margin: 0 0 1rem 1rem;
}

.block span {
    font-size: 20px;
}

.cards {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}


button {
    width: 180px;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    box-shadow: 0 3px 5px #535354;
    position: relative;
    box-sizing: border-box;
    transition: all 500ms ease;
    height: 42px;
    border: none;
    background-color: rgba(0, 0, 0, 0.4);
    color: #f2f2f2;
    border-radius: 5px;
}

button:hover {
    background: rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 3px rgba(0, 0, 0, 0.4);
}
button:active {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, .1);
}

.link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.link button{
    width: 360px;
}
a{
    text-decoration: none;
    color: white
}

img {
    width: 100px;
    height: 100px;
}